<template>
  <div class="grid grid-cols-12 gap-3">
    <div class="col-span-12 xxl:col-span-12 grid grid-cols-12 gap-3">
      <!-- BEGIN: General Report -->
      <div class="col-span-12 mt-4">
        <div class="intro-y flex items-center h-10">
          <h2 class="text-lg font-medium truncate mr-5">{{ listDM.titlePage }}</h2>
          <a 
            href="javascript:;" 
            class="ml-auto flex text-theme-1 dark:text-theme-10"
            @click="getData();"
          >
            <RefreshCcwIcon class="w-4 h-4 mr-3" /> <span class="hidden md:block">Reload Data</span>
          </a>
        </div>
        <div class="grid grid-cols-12 gap-3 mt-3">
          <div
            v-for="(k, idx) in listDM.listTMember"
            :key="idx"
            class="col-span-12 sm:col-span-6 xl:col-span-3 intro-y mb-3"
          >
            <UrlSlug v-if="k.is_internal_link" :to="'/' + role + k.url" >
              <div class="report-box zoom-in">
                <div class="box p-5">
                  <div class="flex  items-center">
                    <p class="text-base font-medium mr-2">{{ k.title }}</p>
                    <div class="ml-auto">
                      <component :is="k.icon" class="w-10 h-10 text-primary" />
                    </div>
                  </div>
                </div>
              </div>
            </UrlSlug>
            <a v-else :href="k.url" :target="k.target">
              <div class="report-box zoom-in">
                <div class="box p-5">
                  <div class="flex  items-center">
                    <p class="text-base font-medium mr-2">{{ k.title }}</p>
                    <div class="ml-auto">
                      <component :is="k.icon" class="w-10 h-10 text-primary" />
                    </div>
                  </div>
                </div>
              </div>
            </a>
          </div>

        </div>
        <div class="grid grid-cols-12 gap-3 mt-3">
          <LoadingDiv id="loadingWTopMember" />
        </div>
        <div class="grid grid-cols-12 gap-2 mt-3">
          <div class="col-span-12 sm:col-span-6 xxl:col-span-6 intro-y mb-1">
            <div class="box pb-3">
              <div class="flex items-center p-5 border-b-2 border-gray-200 dark:border-dark-5">
                <h3 class="text-base font-medium mr-2">Syarat & Ketentuan Garansi</h3>
              </div>
              <div id="ketUji" class="overflow-x-auto box">
                <table class="table">
                  <tbody>
                    <tr
                      v-for="(du, idx) in listDM.listSK"
                      v-bind:key="idx"
                      :class="[( idx % 2 === 0 ? 'bg-gray-200 dark:bg-dark-1' : '')]"
                    >
                      <td class="border-b dark:border-dark-5 w-8 px-0">{{ idx+1 }}</td>
                      <td class="border-b dark:border-dark-5 px-0" v-html="du.value" ></td>
                    </tr>
                  </tbody>
                </table>
                <LoadingDiv id="loadingSK" />
              </div>
            </div>
          </div>
          <div class="col-span-12 sm:col-span-6 xxl:col-span-6 intro-y mb-1">
            <div class="box pb-3">
              <div class="flex items-center p-5 border-b-2 border-gray-200 dark:border-dark-5">
                <h3 class="text-base font-medium mr-2">Tahapan Belajar Di CPNSonline.Id</h3>
              </div>
              <div id="ketUji" class="overflow-x-auto box">
                <table class="table">
                  <tbody>
                    <tr
                      v-for="(du, idx) in listDM.listTB"
                      v-bind:key="idx"
                      :class="[( idx % 2 === 0 ? 'bg-gray-200 dark:bg-dark-1' : '')]"
                    >
                      <td class="border-b dark:border-dark-5 w-8 px-0">{{ idx+1 }}</td>
                      <td class="border-b dark:border-dark-5 px-0" v-html="du.value" ></td>
                    </tr>
                  </tbody>
                </table>
                <LoadingDiv id="loadingTB" />
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- END: General Report -->
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, reactive } from "vue";
import { sk, tahapanBelajar, tryoutPemantapan, topDMember } from "./dashboard-member";
import { api } from "@/utils/api-config";

export default defineComponent({
  setup() {
    const role = api.getRole();
    // console.log("login-data ", $ck.getCookie('login-data'));

    const listDM = reactive({
      titlePage: role === 'member' ? 'Dashboard Member' : 'Dashboard Fitur COID',
      listTMember: [],
      listSK: [],
      listTB: [],
    });

    // for get data widget top
    const getWT = async () => {
      cash('#loadingWTopMember').show();

      const hide = () => cash('#loadingWTopMember').hide();
      const url = await api.getConfigFeatureMember('memberWidgetTop', 'featureWidgetTop');
      const hitApi = await api.hitApiGet(url, hide);
      const rs = hitApi.data.response;
      listDM.listTMember = rs;

    };

    // for get syarat dan ketentuan garansi
    const getSK = async () => {
      cash('#loadingSK').show();

      const hide = () => cash('#loadingSK').hide();
      const url = await api.getConfigFeatureMember('memberSK', 'featureSK');
      const hitApi = await api.hitApiGet(url, hide);
      const rs = hitApi.data.response;
      listDM.listSK = rs;

    };

    // for get data tahapan belajar
    const getTB = async () => {
      cash('#loadingTB').show();

      const hide = () => cash('#loadingTB').hide();
      const url = await api.getConfigFeatureMember('memberTB', 'featureTB');
      const hitApi = await api.hitApiGet(url, hide);
      const rs = hitApi.data.response;
      listDM.listTB = rs;

    };

    const getData = async () => {
      await getWT();
      await getSK();
      await getTB();
    };
    getData();

    return {
      role,
      listDM,
      getData,
    };
  },
  data() {
    return {
      listSK: sk,
      listTB: tahapanBelajar,
      listTP: tryoutPemantapan,
      listTMember: topDMember,
    }
  },
});
</script>
